body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --orange: hsl(26, 100%, 55%);
  --pale-orange: hsl(25, 100%, 94%);

  --very-dark-blue: hsl(220, 13%, 13%);
  --dark-grayish-blue: hsl(219, 9%, 45%);
  --grayish-blue: hsl(220, 14%, 75%);
  --light-grayish-blue: hsl(223, 64%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%)
}
* {
  box-sizing: border-box;
}
body {
  font-size: 16px;
  font-family: 'Kumbh Sans', sans-serif;
  font-weight: 700;
  background-color: hsl(0, 0%, 100%);
  color: hsl(220, 13%, 13%);
}
.container {
  width: 90%;
  margin-right: auto;
  margin-left: auto;
  position: relative;
}
@media (min-width: 1440px) { /* Desktop */
  .container {
    width: 1400px;
  }
}
@media (max-width: 375px) { /* Mobile screen */
  .container {
    width: 90%;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  margin: 0;
  padding: 0;
}

/*
### Primary

- Orange: hsl(26, 100%, 55%)
- Pale orange: hsl(25, 100%, 94%)

### Neutral

- Very vark blue: hsl(220, 13%, 13%)
- Dark grayish blue: hsl(219, 9%, 45%)
- Grayish blue: hsl(220, 14%, 75%)
- Light grayish blue: hsl(223, 64%, 98%)
- White: hsl(0, 0%, 100%)
- Black (with 75% opacity for lightbox background): hsl(0, 0%, 0%)

*/


.Lightbox.active {
  visibility: visible;
}
.Lightbox {
  visibility: hidden;
  position: fixed;
  z-index: 999;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
}
.Lightbox .preview {
  width: 70% !important;
}
.Lightbox.active .preview .image .switch {
  visibility: visible ;
}
.Lightbox .preview .image .switch .previous,
.Lightbox .preview .image .switch .next {
  margin: -5% !important;
}
.Lightbox .preview .image .switch .previous:hover svg path,
.Lightbox .preview .image .switch .next:hover svg path{
  stroke: var(--orange);
}
.Lightbox .preview .thumbnails {
  width: 80%;
  margin: 0 10%;
}
.Lightbox .preview .close {
  text-align: end;
  margin-bottom: 10px;
}
.Lightbox .preview .close svg {
  cursor: pointer;
  transition: .3s;
}
.Lightbox .preview .close svg path {
  fill: var(--white);
}
.Lightbox .preview .close svg:hover path {
  fill: var(--orange);
}
@media (max-width: 375px) {
  .Lightbox {
    display: none;
  }
}
.MainProduct {
  background-color: var(--white);
  padding: 60px 0;
}
.MainProduct .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.MainProduct .preview,
.MainProduct .information {
  width: 50%;
  padding: 7%;
}
.MainProduct img {
  width: 100%;
  height: 100%;
}
.MainProduct .preview img {
  border-radius: 15px;
}
.MainProduct .preview .image {
  margin-bottom: 30px;
  position: relative;
  cursor: pointer;
}
.MainProduct .preview .image .switch {
  visibility: hidden;
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.MainProduct .preview .image .switch .previous,
.MainProduct .preview .image .switch .next{
  background-color: var(--white);
  width: 10%;
  height: 10%;
  text-align: center;
  border-radius: 50%;
  margin: 10px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.MainProduct .preview .image .switch .previous:hover svg path,
.MainProduct .preview .image .switch .next:hover svg path {
  stroke: var(--orange);
}
.MainProduct .preview .image .switch img {
  width: 30%;
  height: auto;
}
.MainProduct .preview .thumbnails {
  display: flex;
  justify-content: space-between;
}
.MainProduct .preview .thumbnails li {
  border-radius: 15px;
  width: 20%;
  border: 2px solid transparent;
  cursor: pointer;
}
.MainProduct .preview .thumbnails li.active {
  background-color: var(--white);
  border: 2px solid var(--orange);
}
.MainProduct .preview .thumbnails li:hover {
  background-color: var(--white);
}
.MainProduct .preview .thumbnails li:hover img, 
.MainProduct .preview .thumbnails li.active img {
  opacity: .5;
}
.MainProduct .information .company{
  text-transform: uppercase;
  color: var(--orange);
}
.MainProduct .information .title {
  font-size: 50px;
  margin: 20px 0 40px;
}
.MainProduct .information .desc {
  color: var(--grayish-blue);
  line-height: 1.7;
}
.MainProduct .information .price {
  margin: 25px 0 35px;
}
.MainProduct .information .price .total {
  display: inline-block;
  font-size: 30px;
  color: var(--black);
  margin-bottom: 10px;
  margin-right: 20px;
}
.MainProduct .information .price .reduction {
  color: var(--orange);
  background-color: var(--pale-orange);
  padding: 5px 8px 5px 5px;
  border-radius: 10px;
  text-transform: uppercase;
  position: relative;
  top: -5px;
}
.MainProduct .information .price .prev {
  display: block;
  color: var(--grayish-blue);
  text-decoration: line-through;
}
.MainProduct .information .checkout {
  display: flex;
}
.MainProduct .information .checkout .edit {
  flex-shrink: 0;
  display: flex;
  justify-content: space-between;
  width: 30%;
  background-color: var(--light-grayish-blue);
  border-radius: 10px;
  margin-right: 15px;
}
.MainProduct .information .checkout .edit span {
  width: 100%;
  text-align: center;
  padding: 20px 0;
}
.MainProduct .information .checkout .edit span:not(:nth-child(2)) {
  cursor: pointer;
}
.MainProduct .information .checkout .edit span img {
  width: 10px;
  height: auto;
}
.MainProduct .information .add {
  width: 70%;
  padding: 20px;
  background-color: var(--orange);
  color: var(--white);
  font-size: 20px;
  font-weight: bold;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0 20px 20px var(--pale-orange);
  transition: .3s;
}
.MainProduct .information .add:hover {
  opacity: .5;
}
.MainProduct .information .add svg {
  margin-right: 15px;
}
.MainProduct .information .add svg path {
  fill: var(--white);
}
.MainProduct .preview .switch {
  display: none;
}
@media (max-width: 375px) {
  .MainProduct {
    padding: 0 0 60px 0;
  }
  .MainProduct .container {
    display: block;
    width: 100%;
  }
  .MainProduct .preview,
  .MainProduct .information {
    width: 100%;
    padding: 0;
  }
  .MainProduct .preview .image {
    margin-top: -10px;
  }
  .MainProduct .preview .image img {
    border-radius: 0;
    width: 100%;
  }
  .MainProduct .preview .image .switch {
    visibility: visible;
  }
  .MainProduct .preview .thumbnails {
    display: none;
  }
  .MainProduct .information {
    padding: 5%;
  }
  .MainProduct .information .title {
    font-size: 30px;
  }
  .MainProduct .information .desc {
    color: var(--grayish-blue);
    line-height: 1.7;
  }
  .MainProduct .information .price {
    width: 100%;
    position: relative;
  }
  .MainProduct .information .price .prev {
    display: inline-block;
    position: absolute;
    top: 6px;
    right: 0;
  }
  .MainProduct .information .checkout {
    display: block;
  }
  .MainProduct .information .checkout .edit,
  .MainProduct .information .checkout .add {
    margin: 10px 0;
    width: 100%;
  }
}
.Basket {
  background-color: var(--white);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.062);
  color: var(--grayish-blue);
  border-radius: 10px;
  position: absolute;
  width: 350px;
  min-height: 200px;
  top: 90px;
  right: -50px;
  z-index: 1;
}
.Basket .title {
  border-bottom: 1px solid var(--light-grayish-blue);
  color: var(--black);
  padding: 20px;
  margin: 0;
}
.Basket .product {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}
.Basket .product .image {
  width: 50px;
  flex-shrink: 0;
}
.Basket .product .image img {
  border-radius: 6px;
  width: 100%;
}
.Basket .product .info {
  margin: 0 10px;
  padding: 0;
  line-height: 1.7;
}
.Basket .product .result {
  color: var(--black);
  margin-left: 8px;
}
.Basket .product .delete {
  cursor: pointer;
}
.Basket .checkout {
  width: 90%;
  padding: 15px;
  margin: 0 20px 20px;
  border-radius: 10px;
  background-color: var(--orange);
  color: var(--white);
  border: none;
  cursor: pointer;
  font-weight: bold;
  font-size: 16px;
}
.Basket .empty {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

@media (max-width: 375px) {
  .Basket {
    position: absolute;
    width: 100%;
    top: 90px;
    right: 0;
    margin: 10px 0 0;
  }
}
.Navbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  border-bottom: 2px solid var(--light-grayish-blue);
}
.Navbar .menu {
  display: none;
  margin-right: 20px;
  cursor: pointer;
  width: 20px;
}
.Navbar .menu img {
  width: 100%;
}
.Navbar .logo {
  letter-spacing: -2px;
}
.Navbar .links {
  width: 100%;
  display: flex;
  margin-left: 30px;
  list-style: none;
  color: var(--grayish-blue);
}
.Navbar .links li {
  padding: 45px 0;
  margin: 0 10px;
  cursor: pointer;
  border-bottom: 3px solid transparent;
}
.Navbar .links .close {
  display: none;
  margin: 15px;
  margin-bottom: 40px;
  cursor: pointer;
}
.Navbar .overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: hsla(0, 0%, 0%, 0.75);
  z-index: 1;
  visibility: hidden;
}

@media (max-width: 375px) { /* Mobile screen */
  .Navbar .menu {
    display: block;
  }
  .Navbar .logo {
    flex-grow: 1;
  }
  .Navbar .links {
    display: block;
    position: fixed;
    margin: 0;
    padding: 15px;
    top: 0;
    left: 0;
    z-index: 99;
    width: 65%;
    height: 100%;
    background-color: var(--white);
    color: var(--black);
    visibility: hidden;
  }
  .Navbar .links.active {
    visibility: visible;
  }
  .Navbar .links li {
    display: block;
    padding: 10px 0;
  }
  .Navbar .links .close {
    display: block;
  }
  .Navbar .overlay {
    visibility: visible;
  }
}
.Navbar .links li.active,
.Navbar .links li:hover {
  color: var(--dark-grayish-blue);
  border-bottom: 3px solid var(--orange);
}
.Navbar .basket {
  margin: 30px;
  cursor: pointer;
  position: relative;
}
@media (max-width: 375px) {
}
.Navbar .basket .number {
  display: none;
}
.Navbar .basket.active .number {
  display: block;
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: var(--orange);
  color: var(--white);
  font-size: 10px;
  padding: 0 6px;
  border-radius: 10px;
}
.Navbar .basket svg:hover path {
  fill: var(--very-dark-blue);
}
.Navbar .basket.active svg path {
  fill: var(--very-dark-blue);
}
.Navbar .avatar {
  width: 80px;
  border-radius: 50%;
  cursor: pointer;
  flex-shrink: 0;
}
.Navbar .avatar img {
  width: 100%;
  height: 100%;
  border: 2px solid transparent;
}
.Navbar .avatar:hover img {
  border: 2px solid var(--orange);
  border-radius: 50%;
}
@media (max-width: 375px) {
  .Navbar .avatar {
    width: 40px;
  }
}

